import React, { useEffect, useState } from "react";
import { createRef, useRef } from "react";
// reactstrap components
import {
  Container
} from "reactstrap";


function Slide3(props) {
  const [date, setDate] = useState([]);
  const [dateBound, setDateBound] = React.useState(15);
  const dateArray = [];
  useEffect(() => {
    getDate(dateBound)
    setDate(dateArray)
  }, []);
  const companies =[{
    price:'80$'
  },
  {
    price:'40$'
  },
  {
    price:'120$'
  },
  {
    price:'95$'
  }]
  const getDate = (i) => {
    for (let index = 0; index < i; index++) {
      var myCurrentDate = new Date();
      var myFutureDate = new Date(myCurrentDate);
      myFutureDate.setDate(myFutureDate.getDate() + index);
      dateArray.push({ date: String(myFutureDate) });
    }
  };
  const DateBox = (prop) => {
    const { data } = prop;
    const [isPressed, setIsPressed] = useState(false)
    const boxStyle = isPressed ?
      { width: '65px', height: '80px', backgroundColor: '#1f5393', border: '1px solid #ef7c3b', borderRadius: '5px' }
      : { width: '65px', height: '80px', border: '1px solid #ef7c3b', borderRadius: '5px' }
    const colorStyle = isPressed ? { color: 'white' } : { color: '#1f5393' }
    return (
      <a href="#" style={{ width: '50px', height: '90px', textDecoration: 'none', margin: 10, borderRadius: '10px' }}>
        <div onClick={() => { setIsPressed(!isPressed) }} style={boxStyle}>
          <div style={{ height: '50%' }} className="d-flex text-align-center justify-content-center">
            <p className=" fw-bold align-self-center" style={colorStyle}>
              {data.date.split(' ')[2] + ' ' + data.date.split(' ')[1]}
            </p>
          </div>
          <div style={{ height: '50%' }} className="d-flex text-align-center justify-content-center">
            <p className=" fw-bold align-self-center" style={colorStyle}>
              {'200$'}
            </p>
          </div>
        </div>
      </a>
    )
  }

  const Company = (data) =>{
const index = data.index
const price = data.data.price
    return(
 <a href="#" style={{ textDecoration: 'none'}}>
        <div className="d-flex justify-content-center w-100" style={{marginBottom:'10px'}}>
<div className="d-flex"  style={{width:'90%',maxWidth:'700px',border:'1px solid',height:'100px',borderRadius:'10px',borderColor:'#ef7c3b'}}>
<div  style={{width:'30%',borderRight:'1px solid',height:'100px',borderColor:'#ef7c3b'}}>

</div>
<div className="d-flex w-100 h-100 align-items-center"  style={{paddingLeft:'30px',paddingRight:'30px',paddingBottom:'20px',height:'100px'}} >
<div className="w-100" style={{alignSelf:'center'}}>
<h5 className="fw-bold" style={{color:'#1f5393'}}>
 Company {index+1}
</h5>
</div>
<div>
<h4 className="fw-bold" style={{color:'#1f5393'}}>
  {price}
</h4>
</div>
</div>
</div>
</div>
 </a>
    )
  }

  return (
    <>
      <div
        style={{
          backgroundColor: 'white',
          minHeight: '60vh',
          width: '100%'
        }}
      >
        <div className="d-flex justify-content-center" style={{marginLeft:'20px',height:'100px'}}>
        <h5 style={{color:'#1f5393',alignSelf:'center'}}> Pick Up Date</h5>
        <div style={{width:'120px',height:'50px',border:'1px solid #ef7c3b',borderRadius:'50px',alignSelf:'center',margin:'10px',marginTop:'0px'}}>

        </div>
        <h5 style={{color:'#1f5393',alignSelf:'center'}}>Drop Off Date</h5>
        <div style={{width:'120px',height:'50px',border:'1px solid #ef7c3b',borderRadius:'50px',alignSelf:'center',margin:'10px',marginTop:'0px'}}>

</div>
        </div>
        <div className='motto d-flex justify-content-between container-fluid' style={{ minWidth: '510px', maxWidth: '50%', overflowX: 'scroll', overflow: 'scroll' }}>
          {date.map((data) => (
            <DateBox data={data} />
          ))}
        </div>
 
    <div style={{paddingBottom:'20px'}}>
      {
        companies.map((data,i)=>(
          <Company data={data} index={i}/>
        ))
      }
   
      
    </div>
  
      </div>
    </>
  );
}

export default Slide3;
