import React, {useCallback, useEffect, useState} from 'react'
import {useDropzone} from 'react-dropzone'

export default function DropZone() {
    const thumbsContainer = {
        display: 'flex',
     
        flexWrap: 'wrap',
        marginTop: 16
      };
      
      const thumb = {
        display: 'inline-flex',
        borderRadius: 2,
        border: '1px solid #eaeaea',
        marginBottom: 8,
        marginRight: 8,
        width: 100,
        height: 100,
        padding: 4,
        boxSizing: 'border-box'
      };
      
      const thumbInner = {
        display: 'flex',
        minWidth: 0,
        overflow: 'hidden'
      };
      
      const img = {
        display: 'block',
        width: 'auto',
        height: '100%'
      };
      
      
    
        const [files, setFiles] = useState([]);
        const {getRootProps, getInputProps} = useDropzone({
          accept: 'image/*',
          multiple:true,
          onDrop: acceptedFiles => {
            setFiles(acceptedFiles.map(file => Object.assign(file, {
              preview: URL.createObjectURL(file)
            })));
          }
        });
        
        const thumbs = files.map(file => (
          <div style={thumb} key={file.name}>
            <div style={thumbInner}>
              <img
                src={file.preview}
                style={img}
              />
            </div>
          </div>
        ));
      
        useEffect(() => {
          // Make sure to revoke the data uris to avoid memory leaks
          files.forEach(file => URL.revokeObjectURL(file.preview));
        }, [files]);
      
  return (
<>
<h6 className="fw-bold" >
   Upload Picture
</h6>
    <section style={{border:'1px solid',borderRadius:10,maxWidth:'800px',height:'150px'}}>
      
    <div {...getRootProps({className: 'dropzone'})} style={{padding:10}}>
      <input {...getInputProps()} />
      <p  className='text-align-center' >Drag 'n' drop some files here, or click to select files</p>
    </div>
    <aside style={thumbsContainer}>
      {thumbs}
    </aside>
  </section>
  </>
  )
}