import React, { useContext, useEffect, useState } from 'react'
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Button from '@mui/material/Button';
import OutlinedInput from '@mui/material/OutlinedInput'
import InputAdornment from '@mui/material/InputAdornment';
import FormHelperText from '@mui/material/FormHelperText';
import FormControl from '@mui/material/FormControl';
import Counter from "../../components/Counter";
import TextField from '@mui/material/TextField';
import { Col, Row } from 'reactstrap';
import DropZone from '../DropZone';
import AddBoxIcon from '@mui/icons-material/AddBox';
import { AppContext } from '../../context/AppContext';

export default function AccordionAddItem(props) {
    const { items, setItems } = useContext(AppContext)
    const [count, setCount] = React.useState(0);
    const [values, setValues] = React.useState( items[props.index]);
    useEffect(()=>{
        items[props.index]=values
    },[values])

    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };


    return (
       
                <Accordion defaultExpanded={props?.expanded} style={{ width: '90%' }}>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header">
                        <Typography>Product {props.index + 1}</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <div style={{ marginLeft: '10px' }} className='w-100' >
                           
                                <h5 className="fw-bold" >
                                    Item Dimensions
                                </h5>
                                <h6 className="fw-bold"  >
                                    Item Name
                                </h6>
                                <TextField id="standard-basic"     
                                value={values.itemName}
                                onChange={handleChange('itemName')}
                                label="Item Name" 
                                variant="standard" 
                                style={{ paddingBottom: '20px', width: '200px' }} />
                              
                          
                               <h6 className="fw-bold">
                                    Size
                                </h6>
                                <Row>
                                    <Col md='3'>
                                        <div>
                                            <OutlinedInput
                                                id="outlined-adornment-weight"
                                                value={values.itemHeight}
                                                onChange={handleChange('itemHeight')}
                                                endAdornment={<InputAdornment position="end">cm</InputAdornment>}
                                                aria-describedby="outlined-weight-helper-text"
                                                inputProps={{
                                                    'aria-label': 'weight',
                                                }}
                                            />
                                            <FormHelperText id="outlined-weight-helper-text">Height</FormHelperText>
                                        </div>
                                    </Col>
                                    <Col md='3'>
                                        <div>
                                            <OutlinedInput
                                                id="outlined-adornment-weight"
                                                value={values.itemWidth}
                                                onChange={handleChange('itemWidth')}
                                                endAdornment={<InputAdornment position="end">cm</InputAdornment>}
                                                aria-describedby="outlined-weight-helper-text"
                                                inputProps={{
                                                    'aria-label': 'weight',
                                                }}
                                            />
                                            <FormHelperText id="outlined-weight-helper-text">Width</FormHelperText>
                                        </div>
                                    </Col>
                                    <Col md='3'>
                                        <div>
                                            <OutlinedInput
                                                id="outlined-adornment-weight"
                                                value={values.itemLength}
                                                onChange={handleChange('itemLength')}
                                                endAdornment={<InputAdornment position="end">cm</InputAdornment>}
                                                aria-describedby="outlined-weight-helper-text"
                                                inputProps={{
                                                    'aria-label': 'weight',
                                                }}
                                            />
                                            <FormHelperText id="outlined-weight-helper-text">length</FormHelperText>
                                        </div>
                                    </Col>
                                </Row>
                                <div className='d-flex' style={{padding:'15px',marginLeft:'-30px'}}>
                                <Counter count={values.itemQuantity} title={'Quantity'} 
                     
                               setCount={(e)=>{setValues({ ...values, itemQuantity: e})} }
                                />
                                <Counter count={values.itemWeight}  title={'Weight'} 
                            
                            setCount={(e)=>{setValues({ ...values, itemWeight: e})} }
                                />
                                </div>
                            
                        
                            <DropZone />
   
   <div style={{marginTop:'15px'}} className='w-100'>
   <h6 className="fw-bold " >
                                    Special Notes
                                </h6>
   <TextField
             value={values.note}
             onChange={handleChange('note')}
   id="outlined-multiline-flexible"
   label="Notes"
   multiline
   rows={4}
   style={{width:'100%',maxWidth:'800px'}}
        />
   </div>
                        </div>
                        <div style={{ padding: '30px' }} className='d-flex w-100 justify-content-center  align-items-center'>
                            <Button onClick={() => { setItems(oldArray => [...oldArray, {
        itemName: '',
        itemHeight: '',
        itemWidth: '',
        itemWeight: 0,
        itemLength: '',
        itemNotes: '',
        count:'',
        itemQuantity:0,
        note:''

    }]); }} style={{ backgroundColor: '#ef7c3b', maxWidth: '220px', height: '60px', minWidth: '100px',borderRadius:'10px' }} variant="contained">
                                <AddBoxIcon style={{ marginRight: '10px' }} />
                                Add Another Item</Button>
                        </div>
                    </AccordionDetails>
                </Accordion>
         
    )
}
