import React from 'react'
import { useRef } from 'react';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepButton from '@mui/material/StepButton';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { Col, Row } from 'reactstrap';
import Slide3 from './Slide3';
import { SnapList, SnapItem, useVisibleElements, useScroll, useDragToScroll } from 'react-snaplist-carousel';
import Slide1 from './Slide1';
import Slide2 from './Slide2';
import Slide4 from './Slide4';
import Header from '../components/Header';

export default function Steper() {
    const [activeStep, setActiveStep] = React.useState(0);
    const [completed, setCompleted] = React.useState({});
    const steps = ['Pickup details', 'Product details', 'Set Schedule', 'Create order'];

    const totalSteps = () => {
        return steps.length;
    };

    const completedSteps = () => {
        return Object.keys(completed).length;
    };

    const isLastStep = () => {
        return activeStep === totalSteps() - 1;
    };

    const allStepsCompleted = () => {
        return completedSteps() === totalSteps();
    };

    const handleNext = () => {
        const newActiveStep =
            isLastStep() && !allStepsCompleted()
                ? // It's the last step, but not all steps have been completed,
                // find the first step that has been completed
                steps.findIndex((step, i) => !(i in completed))
                : activeStep + 1;
        setActiveStep(newActiveStep);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleStep = (step) => () => {
        setActiveStep(step);
    };

    const handleComplete = () => {
        const newCompleted = completed;
        newCompleted[activeStep] = true;
        setCompleted(newCompleted);
        handleNext();
    };

    const StepFunc = () => {

        switch (activeStep) {
            case 0:
                return <Slide1 />

            case 1:
                return <Slide2 />

            case 2:
                return <Slide3 />

            case 3:
                return <Slide4 />

            default:
                break;
        }
    };

    return (
        <div style={{ backgroundColor: '#1f4393', height: '100vh' }}>
       
            <div className='d-flex justify-content-center' >
                <div style={{ width: '900px' }} >
                    <Stepper sx={{ paddingTop: '30px' }} nonLinear activeStep={activeStep}>
                        {steps.map((label, index) => (
                            <Step key={label} completed={completed[index]}>
                                <StepButton style={{ textAlign: 'center', color: 'white' }} onClick={handleStep(index)} />
                                <h5 style={{ textAlign: 'center', color: 'white',fontSize:'20px',lineHeight:'22px'}}>
                                    {label}

                                </h5>
                            </Step>
                        ))}
                    </Stepper>
                </div>
            </div>
            <br />
            <br />
            <div style={{ alignItems: 'center', backgroundColor: '#1f4393', }}>
                <StepFunc />
                <div className='d-flex' style={{ backgroundColor: '#1f4393', flex: 1 }}>
                    {activeStep != 0 ? <div style={{ padding: '30px', paddingTop: '20px', backgroundColor: '#1f4393' }} className='d-flex w-100 justify-content-start  align-items-center'>
                        <Button onClick={() => { setActiveStep(activeStep - 1) }} style={{ backgroundColor: '#ef7c3b', maxWidth: '180px', minWidth: '100px', height: '60px' }} variant="contained">Back</Button>
                    </div> : null}
                    <div style={{ padding: '30px', paddingTop: '20px', backgroundColor: '#1f4393' }} className='d-flex w-100 justify-content-end  align-items-center'>
                        <Button onClick={() => { setActiveStep(activeStep + 1) }} style={{ backgroundColor: '#ef7c3b', maxWidth: '180px', height: '60px', minWidth: '100px' }} variant="contained">{activeStep != steps.length-1 ?'Continue':'Place Order'}</Button>
                    </div>
                </div>
            </div>
        </div>)
}
