import React from 'react'
import IconButton from '@mui/material/IconButton';
import AddBoxIcon from '@mui/icons-material/AddBox';
import IndeterminateCheckBoxIcon from '@mui/icons-material/IndeterminateCheckBox';
import '../../App.scss'
export default function Counter(props) {
const {count,setCount,title}=props
  return (

 <div>
      <h6 style={{marginBottom:'-10px',marginLeft:'15px'}} className='fw-bold'>
    {title}
  </h6>
    <div className="d-flex" style={{borderRadius:5,maxWidth:'305px' }}>
     
    <IconButton  onClick={count>0?()=>{setCount(count-1)}:()=>{}} aria-label="delete" size="large">
       <IndeterminateCheckBoxIcon fontSize="large"  style={{color:'#1f4393'}} />
     </IconButton>
   
 <input  type={'number'} value={count} onChange={(e)=>{setCount(Number(e.target.value))}} style={{height:'40px',alignSelf:'center',textAlign:'center',border:'0px',width:'70px',fontSize:'20px'}}/>
 
     <IconButton onClick={()=>{setCount(count+1)}} aria-label="delete" size="large">
       <AddBoxIcon fontSize="large" style={{color:'#1f4393'}}  />
     </IconButton>
    </div>
 </div>

  )
}
